
import useSound from "use-sound"
import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";

const PlayersFilter = ({ selectFilter, filter }) => {

    const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );
    const [ playSelect ] = useSound( SelectSfx, {volume:1} );

    const select = ( e ) => {
        selectFilter( e );
        playSelect();
    }

    return (
        <div className="tw-flex tw-flex-row tw-justify-around tw-py-2">
            <div onClick={ () => select('all') } className={`player-list-item-name ${filter === "all" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                ALL
            </div>
            <div onClick={ () => select('PC') } className={`player-list-item-name ${filter === "PC" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                STEAM
            </div>
            <div onClick={ () => select('PSN') } className={`player-list-item-name ${filter === "PSN" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                PSN
            </div>
            <div onClick={ () => select('XBOX') } className={`player-list-item-name ${filter === "XBOX" ? "active-player-list-item" : "inactive-player-list-item"}`} onMouseEnter={ playNavigate } >
                XBOX
            </div>
        </div>
    )
}

export default PlayersFilter
