
import {
	Chart as ChartJS, RadialLinearScale, PointElement, LineElement, Filler, Tooltip, Legend, CategoryScale, LinearScale, BarElement, Title
} from 'chart.js';

import { useState, useEffect } from 'react';
import { Bar } from 'react-chartjs-2';
import { Radar } from 'react-chartjs-2';

ChartJS.register(
	RadialLinearScale, PointElement, LineElement, Filler, Tooltip, Legend, CategoryScale, LinearScale, BarElement, Title,
);

const RadarChart = ({ statistics, average }) => {

	const [movementData, setMovementData] = useState({
		labels: ['BOOST', 'DRIFT', 'CONSISTENCY', 'BACKWARD FLYING', 'STRATEGY', ],
		datasets: [{data: [0, 0, 0, 0, 0]}]
	});
	const [missileData, setMissileData] = useState({
		labels: ['', 'ACCURACY', 'CURVING', 'LOOPING'],
		datasets: [{data: [0, 0, 0, 0]}]
	});
	const [machineGunData, setMachineGunData] = useState({
		labels: ['', 'ACCURACY', 'USE RATE'],
		datasets: [{data: [0, 0, 0]}]
	});

	useEffect(() => {
		if (statistics) {

			let bgColor = "";

			if (average > 97) {
				// SS
				bgColor = 'rgba(0, 176, 240, 0.5)'
			} else if (97 >= average && average > 80) {
				// S
				bgColor = 'rgba(0, 176, 80, 0.5)'
			} else if (80 >= average && average > 60) {
				// A
				bgColor = 'rgba(146, 208, 80, 0.5)'
			} else if (60 >= average && average > 40) {
				// B
				bgColor = 'rgba(255, 192, 0, 0.5)'
			} else if (40 >= average) {
				// C
				bgColor = 'rgba(255, 0, 0, 0.5)'
			} else {
				bgColor = 'rgba(0, 0, 0, 0.5)'
			}

			let newData = {
				labels: ['BOOST', 'DRIFT', 'CONSISTENCY', 'BACKWARD FLYING', 'STRATEGY', ],
				datasets: [
					{
						label: "",
						data: [
							statistics.boost,
							statistics.drift,
							statistics.consistency,
							statistics.bkf,
							statistics.strategy,
						],
						backgroundColor: bgColor,
						fill: true,
						borderColor: 'rgba(255, 255, 255, 1)',
						borderWidth: 1,
						pointBackgroundColor: 'rgb(0, 176, 240)',
						pointBorderColor: '#fff',
						pointHoverBackgroundColor: '#fff',
						pointHoverBorderColor: 'rgb(0, 176, 240)',
						pointRadius: 3,
					}
				]
			};

			let dataB = {
				labels: ['', 'ACCURACY', 'CURVING', 'LOOPING'],
				datasets: [
					{
						label: '',
						data: [100, statistics.mslAcc, statistics.mslCurving, statistics.mslLooping],
						backgroundColor: [
							'rgba(0, 0, 0, 0.0)',
							generateColorBasedOnPunctuation( statistics.mslAcc ),
							generateColorBasedOnPunctuation( statistics.mslCurving ),
							generateColorBasedOnPunctuation( statistics.mslLooping )
						]
					}
				],
			};

			let dataC = {
				labels: ['', 'ACCURACY', 'USE RATE'],
				datasets: [
					{
						label: '',
						data: [100, statistics.mgAcc, statistics.mgRate],
						backgroundColor: [
							'rgba(0, 0, 0, 0.0)',
							generateColorBasedOnPunctuation( statistics.mgAcc ),
							generateColorBasedOnPunctuation( statistics.mgRate )
						]
					}
				],
			};

			setMovementData(newData);
			setMissileData(dataB);
			setMachineGunData(dataC);
		}
	}, [statistics, average]);


	const generateColorBasedOnPunctuation = ( pts ) => {
		let bgColor = "";

		if (pts > 97) {
			// SS
			bgColor = 'rgba(0, 176, 240, 0.6)'
		} else if (97 >= pts && pts > 80) {
			// S
			bgColor = 'rgba(0, 176, 80, 0.6)'
		} else if (80 >= pts && pts > 60) {
			// A
			bgColor = 'rgba(146, 208, 80, 0.6)'
		} else if (60 >= pts && pts > 40) {
			// B
			bgColor = 'rgba(255, 192, 0, 0.6)'
		} else if (40 >= pts) {
			// C
			bgColor = 'rgba(255, 0, 0, 0.6)'
		} else {
			bgColor = 'rgba(0, 0, 0, 0.6)'
		}

		return bgColor;
	}

	const options = {
		title: {
			display: true,
		},
		scales: {
			r: {
				angleLines: {
					display: true,
					color: 'gray'
				},
				grid: {
					color: 'gray'
				},
				pointLabels: {
					color: 'white'
				},
				suggestedMin: 0,
				suggestedMax: 100,
				ticks: {
					showLabelBackdrop: false,
					stepSize: 20
				}
			},
		},
		elements: {
			line: {
				borderWidth: 3
			}
		},
		plugins: {
			title: {
				display: true,
				text: 'MANEOUVERS',
				color: 'white'
			},
			legend: {
				display: false
			}
		}
	};

	const indexAxis = 'y';
	const mantainAspectRatio = false;
	const scales = {
		y: {
			stacked: true,
			beginAtZero:true,
			grid: {
				color: 'gray',
				offset: true
			},
			pointLabels: {
				color: 'white'
			},
			suggestedMin: 0,
			min: 0,
			suggestedMax: 10,
			max: 10,
			ticks: {
				stepSize: 1,
				color:"white"
			}
		}
	};
	const responsive = true;
	const legend = {
		display: false
	}

	let optionsB = {
		indexAxis:indexAxis,
		mantainAspectRatio:mantainAspectRatio,
		scales:scales,
		responsive:responsive,
		plugins: {
			legend:legend,
			title: {
				display: true,
				text: 'MISSILE CONTROL',
				color: 'white'
			}
		}
	};
	let optionsC = {
		indexAxis:indexAxis,
		mantainAspectRatio:mantainAspectRatio,
		scales:scales,
		responsive:responsive,
		plugins: {
			legend:legend,
			title: {
				display: true,
				text: 'MACHINE GUN CONTROL',
				color: 'white'
			}
		}
	};

	return (
		<div className="tw-flex lg:tw-flex-row tw-flex-col tw-justify-center">
			<div className="bar-container">
				<Bar options={optionsB} data={missileData} />
				<div className="tw-my-4"></div>
				<Bar options={optionsC} data={machineGunData} />
			</div>
			<div className="radar-container tw-pl-2">
				<Radar data={movementData} options={options} />
			</div>
		</div>
	)
}

export default RadarChart
