
import { Link } from "react-router-dom"

import useSound from "use-sound"
import CancelSfx from "../sfx/cancel.mp3";

const Breadcumb = ({ iconSrc, secondLevel, thirdLevel }) => {
    let secTier, thrTier;
    if ( secondLevel ) {
        secTier = <p className="tw-text-sm">&gt; &gt; { secondLevel }</p>;
    }
    if ( thirdLevel ) {
        thrTier = <p className="tw-text-sm">&gt; &gt; &gt; { thirdLevel }</p>;
    }

    const [ playCancel ] = useSound( CancelSfx, {volume:1} );

    return (
        <div className="tw-flex tw-flex-row tw-py-8">
            <div className="breadcumb-logo">
                <img src={iconSrc} height="59" width="59" alt="ico"></img>
            </div>
            <div className="tw-flex-column tw-px-8">
                <p  className="tw-text-sm">
                    <Link to="/" onClick={ playCancel } > 
                        <span className="lg:tw-hidden tw-block">&gt; AC7 PSM MP Ranking</span>
                        <span className="tw-hidden lg:tw-block">&gt; Ace Combat 7 PSM Multiplayer Ranking</span>
                    </Link>
                </p>
                { secTier }
                { thrTier }
            </div>
        </div>
    )
}

export default Breadcumb
