
import { useEffect } from "react";

import BGM1 from "../../sfx/AC2-Hanger.mp3";
import BGM2 from "../../sfx/AC2-Lode Stone.mp3";
import BGM3 from "../../sfx/AC4-Scramble.mp3";
import BGM4 from "../../sfx/AC4-The Hangar.mp3";
import BGM5 from "../../sfx/AC5-Hangar 1.mp3";
import BGM6 from "../../sfx/AC5-Tutorial Main.mp3";
import BGM7 from "../../sfx/AC7-444th Air Base Hangar.mp3";
import BGM8 from "../../sfx/ACZ-Briefing I.mp3";
import BGM9 from "../../sfx/ACZ-Hangar.mp3";
import BGM10 from "../../sfx/ACZ-Sortie I.mp3";

const Music = ({ musicState }) => {
	const shuffle = (array) => {
		let currentIndex = array.length, randomIndex;
		// While there remain elements to shuffle...
		while (currentIndex !== 0) {
			// Pick a remaining element...
			randomIndex = Math.floor(Math.random() * currentIndex);
			currentIndex--;

			// And swap it with the current element.
			[array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
		}
		return array;
	}

	let trackList = [BGM1, BGM2, BGM3, BGM4, BGM5, BGM6, BGM7, BGM8, BGM9, BGM10];
	let currentTrack = 0;

	shuffle( trackList );

	let audio = new Audio(trackList[0]);
	audio.volume = 0.5;

	useEffect(() => {
		document.addEventListener("keydown", togglePlay);
		audio.addEventListener("ended", playNextSong);
		//playPauseMusic();
	}, [musicState]);

	const togglePlay = (event) => {
		if (event.keyCode === 119) {
			event.preventDefault();
			playPauseMusic();
		}
	}

	const playPauseMusic = () => {
		musicState = !musicState;
		musicState ? audio.play() : audio.pause();
	}

	const playNextSong = () => {
		if (currentTrack < 9) {
			currentTrack = currentTrack + 1;
		} else if (currentTrack === 9) {
			currentTrack = 0;
		}

		audio.src = trackList[currentTrack];
		audio.play();
	}

	return (
		<div onKeyPress={togglePlay}></div>
	)
}

export default Music
