
import './styles.css'

import { useState, useEffect } from 'react';

import { Link } from "react-router-dom"
import useSound from "use-sound"
import NavigateSfx from "../../sfx/navigate.mp3";
import SelectSfx from "../../sfx/select.mp3";

import KeyActionSuggestion from './KeyActionSuggestion';

const HomePage = () => {
    
    const [ playNavigate ] = useSound( NavigateSfx, {volume:1} );
    const [ playSelect ] = useSound( SelectSfx, {volume:1} );
    const [ helpMessage ] = useState('Fullscreen mode will give you a better experience');
    
    useEffect(() => {
        document.title = `Ace Combat 7 PSM Multiplayer Ranking`;
    });

    const emulateF8Toggle = () => {
        window.dispatchEvent(new KeyboardEvent('keydown', {
            'key': 'F8'
        }));
    }

    return (
        <div className="tw-h-screen tw-w-screen tw-flex tw-flex-row tw-justify-center">
            <div className="main-home-page-container tw-flex tw-flex-col tw-w-9/12 tw-p-4">
                <h1 className="tw-text-3xl lg:tw-text-5xl tw-py-20">Ace Combat 7 PSM Multiplayer Ranking</h1>
                <ul>
                    <li className="tw-text-2xl inactive-list-item">
                        <Link to="/pilots" onMouseEnter={ playNavigate } onClick={ playSelect }>Pilots</Link>
                    </li>
                    
                    <li className="tw-text-2xl inactive-list-item">
                        <Link to="/about" onMouseEnter={ playNavigate } onClick={ playSelect }>About</Link>
                    </li>
                </ul>
                <div className="tw-flex tw-my-auto">

                </div>
                <div className="tw-flex tw-flex-row tw-justify-end tw-py-16">
                    <p className="tw-text-xl tw-text-right">
                        Fullscreen mode will give you a better experience.<br />
                        Close fullscreen to exit.
                    </p>
                </div>
                <div className="tw-flex tw-flex-row">
                    <KeyActionSuggestion keyName="F11" description="Fullscreen" />
                    <KeyActionSuggestion keyName="F8" description="Play/Pause BGM" onClick={emulateF8Toggle}/>
                </div>
            </div>
        </div>
    )
}

export default HomePage
