

const KeyActionSuggestion = ({ keyName, description }) => {
    return (
        <div className="tw-flex tw-flex-row tw-items-center">
            <div className="keycap-box tw-flex tw-justify-center">
                <span>{keyName}&nbsp;</span>
            </div>
            <span className="tw-text-xl tw-pl-2 tw-pr-6">{description}</span>
        </div>
    )
}

export default KeyActionSuggestion
