
import './styles.css';

import { useState, useEffect } from 'react';
import PlayerListItem from './PlayerListItem';
import PlayersFilter from './PlayersFilter';

const PlayerList = ({ vctPlayers, onSelectPilot, showForMobile }) => {
    useEffect(()=>{
        if ( vctPlayers ) {
            if ( vctPlayers.length > 0 ) {
                selectFilter('all');
            }
        }
    },[vctPlayers]);

    const [filteredPlayers, setFilteredPlayers] = useState(vctPlayers)
    const [selectedFilter, setSelectedFilter] = useState('')

    const selectFilter = ( filter ) => {
        setSelectedFilter( filter );

        if ( vctPlayers ) {
            let newPlayers = [];

            if ( filter === 'all' ) {
                newPlayers = vctPlayers.slice()
            } else {
                for (const player of vctPlayers) {
                    if ( player.platforms.indexOf( filter ) > -1 ) {
                        newPlayers.push( player ); 
                    }
                }
            }

            setFilteredPlayers( newPlayers );
        }
    }

    return (
        <div className="pilot-list-container tw-flex tw-flex-col tw-flex-grow tw-px-4 tw-py-32 tw-h-screen">
            <PlayersFilter selectFilter={ selectFilter } filter={ selectedFilter } />
            
            <div className="tw-overflow-visible tw-overflow-y-auto tw-scrollbar-thin tw-scrollbar-thumb-blue-700 tw-scrollbar-track-blue-300">
                { filteredPlayers.map( (player) => ( 
                    <PlayerListItem key={player.id} player={player} onSelect={ onSelectPilot } />
                ))}
            </div>
        </div>
    )
}

export default PlayerList
