
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { useState, useEffect } from 'react';

import HomePage from "./components/home/HomePage";
import PilotsPage from "./components/players/PilotsPage";
import SettingsPage from './components/configuration/SettingsPage';
import AboutPage from './components/about/AboutPage';

import Music from './components/background-music/Music';
import useSound from 'use-sound';
import LoadedAppSfx from "./sfx/load_complete.mp3";
import SelectLoadSfx from "./sfx/select_load.mp3";

import ParticlesBg from "particles-bg";

function App() {

	const [playLoadedApp] = useSound(LoadedAppSfx, {volume:1});
	const [playLoadingApp] = useSound(SelectLoadSfx, {volume:1});
	const [playingMusic] = useState(false);

	const authenticate = () => {
		return new Promise(resolve => setTimeout(resolve, 1000)) // 2 seconds
	}

	useEffect(() => {
		if ( window.location.pathname !== '/' ) {
			window.location.replace('/');
		}
		
		authenticate().then(() => {
			const ele = document.getElementById('ipl-progress-indicator')
			if (ele) {
				playLoadingApp();
				// fade out
				ele.classList.add('available')
				setTimeout(() => {
					// remove from DOM
					ele.outerHTML = ''
				}, 2000)
			}
		})
	});


	let config = {
		num: [4, 7],
		rps: 0.1,
		radius: [5, 40],
		life: [1.5, 3],
		v: [2, 3],
		tha: [-40, 40],
		alpha: [0.6, 0],
		scale: [.1, 0.4],
		position: "fixed",
		color: ["random", "#AAA"],
		cross: "dead",
		// emitter: "follow",
		random: 15
	};

	const onBegin = (evt) => {
		const ele = document.getElementById('modal-begin')
		if (ele) {
			// fade out
			ele.classList.add('available')
			ele.outerHTML = ''

			var eMusic = new Event("keydown");
			eMusic.which = 119;
			eMusic.keyCode = 119;
			document.dispatchEvent(eMusic);

			/* View in fullscreen */
			if ( process.env.NODE_ENV !== 'development' ) {
				var elem = document.documentElement;
				if (elem.requestFullscreen) {
					elem.requestFullscreen();
				} else if (elem.webkitRequestFullscreen) { // Safari
					elem.webkitRequestFullscreen();
				} else if (elem.msRequestFullscreen) { // IE11
					elem.msRequestFullscreen();
				}
			}

			playLoadedApp();
		}
	}

	return (
		<Router>
			<div className="App">
				<div id="modal-begin" className="tw-w-screen tw-h-screen tw-absolute tw-flex tw-flex-col tw-p-4">
					<div className="tw-flex tw-flex-col tw-mx-auto tw-my-auto tw-justify-center">
						<p className="tw-text-2xl tw-text-center" onClick={onBegin}>
							Click Here to begin
						</p>
					</div>
				</div>

				<Routes>
					<Route path='/' element={<HomePage />} />
					<Route path='/pilots' element={<PilotsPage />} />
					<Route path='/settings' element={<SettingsPage />} />
					<Route path='/about' element={<AboutPage />} />
				</Routes>
			</div>
			<ParticlesBg type="cobweb" color="#A0A0A0" config={config} bg={{
				width: "100%",
				height: "100%",
				position: "fixed",
				zIndex: -1,
				top: "0px",
				left: "0px",
			}} />
			<Music musicState={playingMusic} />
		</Router>
	);
}
// https://youtu.be/w7ejDZ8SWv8?t=2982

export default App;
