

const InformationButton = ({ onMouseEnterAction, onClickAction }) => {
    return (
        <div className="tw-flex tw-flex-row">
            <p className="inactive-player-list-item tw-text-base" onMouseEnter={ onMouseEnterAction } onClick={ onClickAction }>[INFORMATION]</p>
        </div>
    )
}

export default InformationButton
