
import { useEffect } from "react";

import Breadcumb from "../Breadcumb"

import AboutLogo from "../../icons/about-icon.svg";

const AboutPage = () => {
    useEffect(() => {
        document.title = `About - AC7 PSM MP Ranking`;
    });

    return (
        <div className="tw-flex tw-flex-row tw-h-screen tw-w-screen tw-justify-center">

            <div className="tw-flex tw-flex-col tw-w-8/12 tw-p-4">
                <Breadcumb secondLevel="About" iconSrc={ AboutLogo }></Breadcumb>
                <div className="tw-flex tw-flex-col tw-mx-auto tw-my-auto tw-justify-center">
                    <p className="tw-text-2xl tw-text-center">
                        MOTIVATION <br />
                        Giquo was bored and decided to learn how to program with ReactJS.<br />
                        Thank you, I hope you enjoy. <br />
                        God bless you.
                    </p>
                    <p className="tw-pt-12 tw-text-2xl tw-text-center">
                        DISCLAIMER: <br />
                        The rank and statistics determination was made based on fighting <br />
                        experiences from me and other people in the community. <br />
                        Is not the purpose to upset anybody, but to keep the fun and competitivity.
                    </p>
                    <p className="tw-pt-12 tw-text-lg tw-text-center">
                        THANKS: <br />
                        John Smith, Fluffy Marsmallows, Talisman, Syncs, Darkskryll <br />
                        I'm grateful for your feedback to make this the most great <br />
                        User experience possible (and surprise) for everybody.<br />
                        :)
                    </p>
                </div>
            </div>
        </div>
    )
}

export default AboutPage
